<template>
  <div class="ds-control ds-form-item ds-cascader" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <span :title="value | proxy(formatter)">{{
        value | proxy(formatter)
      }}</span>
      <div v-if="errors.length > 0" class="ds-error-text">{{ errorsMsg }}</div>
    </template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
        <el-cascader
          v-if="!readOnly"
          v-model="inputValue"
          :options="items"
          :disabled="disabled"
          :filterable="filterable"
          :placeholder="placeholder"
          :clearable="clearable"
          :props="props"
          @change="change"
          @blur="blur"
        ></el-cascader>
        <!--div-->
        <div v-else-if="readOnly" class="ds-form-readonly">
          <span>{{ value | proxy(formatter) }}</span>
        </div>
        <div v-if="errors.length > 0" class="ds-error-text">
          {{ errorsMsg }}
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import formControl from "../../../mixins/formControl";

const options = [
  {
    value: "1",
    label: "",
    children: [
      {
        value: "1-1",
        label: "",
        children: [
          {
            value: "1-1-1",
            label: "",
          },
          {
            value: "1-1-2",
            label: "",
          },
          {
            value: "1-1-3",
            label: "",
          },
          {
            value: "1-1-4",
            label: "",
          },
        ],
      },
      {
        value: "1-2",
        label: "",
        children: [
          {
            value: "1-2-1",
            label: "",
          },
          {
            value: "1-2-2",
            label: "",
          },
        ],
      },
    ],
  },
  {
    value: "2",
    label: "",
    children: [
      {
        value: "2-1",
        label: "Basic",
        children: [
          {
            value: "2-1-1",
            label: "Layout ",
          },
          {
            value: "2-1-2",
            label: "Color ",
          },
          {
            value: "2-1-3",
            label: "Typography ",
          },
          {
            value: "2-1-4",
            label: "Icon ",
          },
          {
            value: "2-1-5",
            label: "Button ",
          },
        ],
      },
      {
        value: "2-2",
        label: "Form",
        children: [
          {
            value: "2-2-1",
            label: "Radio ",
          },
          {
            value: "2-2-2",
            label: "Checkbox ",
          },
          {
            value: "2-2-3",
            label: "Input ",
          },
          {
            value: "2-2-4",
            label: "InputNumber ",
          },
          {
            value: "2-2-5",
            label: "Select ",
          },
          {
            value: "2-2-6",
            label: "Cascader ",
          },
          {
            value: "2-2-7",
            label: "Switch ",
          },
          {
            value: "2-2-8",
            label: "Slider ",
          },
          {
            value: "2-2-9",
            label: "TimePicker ",
          },
          {
            value: "2-2-10",
            label: "DatePicker ",
          },
          {
            value: "2-2-11",
            label: "DateTimePicker ",
          },
          {
            value: "2-2-12",
            label: "Upload ",
          },
          {
            value: "2-2-13",
            label: "Rate ",
          },
          {
            value: "2-2-14",
            label: "Form ",
          },
        ],
      },
      {
        value: "2-3",
        label: "Data",
        children: [
          {
            value: "2-3-1",
            label: "Table ",
          },
          {
            value: "2-3-2",
            label: "Tag ",
          },
          {
            value: "2-3-3",
            label: "Progress ",
          },
          {
            value: "2-3-4",
            label: "Tree ",
          },
          {
            value: "2-3-5",
            label: "Pagination ",
