            label: "Collapse ",
          },
        ],
      },
    ],
  },
  {
    value: "3",
    label: "",
    children: [
      {
        value: "3-1",
        label: "Axure Components",
      },
      {
        value: "3-2",
        label: "Sketch Templates",
      },
      {
        value: "3-3",
        label: "",
      },
    ],
  },
];

export default dsf.component({
  name: "DsfCascader",
  mixins: [formControl],
  ctrlCaption: "",
  props: {
    simple: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: "",
    },
    // 
    value: {
      type: [Object, Array],
      required: false
    },
    // 
    items: {
      type: Array,
      default() {
        return [];
      }
    },
    filterable: {
      type: Boolean,
      default: false,
      desc: ""
    },
    clearable: {
      type: Boolean,
      default: true,
      desc: ""
    },
    showAllLevels: {
      type: Boolean,
      default: true,
      desc: ""
    },
    collapseTags: {
      type: Boolean,
      default: false,
      desc: "Tag"
    },
    // props
    expandTrigger: {
      type: String,
      default: 'click',
      desc: " click / hover"
    },
    multiple: {
      type: Boolean,
      default: false,
      desc: ""
    },
    checkStrictly: {
      type: Boolean,
      default: false,
      desc: ""
    },
    propsValue: {
      type: String,
      default: 'value',
      desc: ""
    },
    propsLabel: {
      type: String,
      default: 'text',
      desc: ""
    },
    propsChildren: {
      type: String,
      default: 'children',
      desc: ""
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      },
    },
  },
  data() {
    return {
      valueCopy: null
    };
  },
  computed: {
    props() {
      return {
        expandTrigger: this.expandTrigger === 'click' ? 'click' : 'hover',
        checkStrictly: this.checkStrictly,
        multiple: this.multiple,
        value: this.propsValue,
        label: this.propsLabel,
        children: this.propsChildren
      }
    },
    $items() {
      let items = this.items || []
      //
      this.initValue(this.value);
      return items;
    },
    inputValue: {
      get() {
        if (dsf.isUnDef(this.value)) {
          return this.multiple ? [] : null;
        }
        if (this.multiple) {
          if (dsf.type(this.value) == "array") {
            return this.value.map(v => v.value.split('/'));
          } else if (dsf.type(this.value) == "object") {
            return [this.value.value.split('/')];
          }
        } else {
          if (dsf.type(this.value) == "array") {
            return this.value[0].value.split('/');
          } else if (dsf.type(this.value) == "object") {
            return this.value.value.split('/');
          }
        }
        return this.multiple ? [] : null;
      },
      set(v) {
        if (!v || !v.length) {
          this.emitValueChange(this.multiple ? [] : null);
          return;
        }
        if (!this.multiple) {
          let val = this.getValueByPath(v);
          this.emitValueChange(val);
        } else {
          let val = v.map(v2 => this.getValueByPath(v2));
          this.emitValueChange(val);
        }
      }
    }
  },
  methods: {
    getValueByPath(path) {
      let text = [], value = [];
      let items = this.items;
      for (let i = 0; i < path.length; i++) {
        let p = path[i];
        if (items == null) {
          return null;
        } else {
          for (let i = 0; i < items.length; i++) {
            let item = items[i];
            if (item[this.propsValue] === p) {
              value.push(p);
              text.push(item[this.propsLabel]);
              if (item[this.propsChildren]) {
                items = item[this.propsChildren];
              } else {
                items = null;
              }
              break;
            }
          }
        }
      }
      return {
        text: text.join('/'),
        value: value.join('/')
      }
    },
    change() {
      this.$dispatch("change", this.value);
      this.$nextTick(() => {
        this.emitFormValidate(this.value);
      })
    },
    blur(){
      this.$dispatch("blur");
    }
  },
});
</script>
