<template>
  <div v-if="isDesign" class="ds-control ds-form-item ds-design-hide ds-textbox" :class="getCss">
    <template>
      <label v-if="showLabel" class="ds-form-label"></label>
      <div class="ds-form-block">
        <el-input v-model="chooseFlow.name" type="text" :disabled="true" />
      </div>
    </template>
  </div>
</template>
<script>
import formControl from "../../../mixins/formControl";
export default dsf.component({
  name: "DsfFlowInfo",
  mixins: [formControl],
  ctrlCaption: "",
  props: {
    caption: {
      type: String,
      default() {
        return "wfinfo";
      },
    },
    chooseFlow: {
      type: Object,
      default() {
        return {};
      },
    },
    eventName: {
      type: String,
      default() {
        return "";
      },
    },
    linkData: {
      type: Array,
      default() {
        return [];
      },
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("flow-info-meta-data");
      },
    },
  },
  data() {
    return {
      _RequestID: null,
      flowSendOverEvent: "",
    };
  },
  watch: {
    chooseFlow(val) {
      const value = {
        wf_id: val.ID,
        wf_name: val.name,
      };
      this.emitValueChange(value);
    },
  },
  computed: {},
  mounted() {},
  destroyed() {},
  methods: {
    linkSetChooseFlow() {
      document.querySelector("[a='linkData2'] .ds-button").click();
    },
    flowSend(res, { line, flowSendOverEvent }) {
      this.flowSendOverEvent = flowSendOverEvent;
      // 
      let id = this.chooseFlow.dsfa_rm_id;
      if (id) {
        // 
        let _this = this,
          // loadingIndex = dsf.layer.loadding(),
          url = "wfr/startSend",
          param = {
            sWFID: id,
            sObjectID: res._id,
            sObjectName: this.eventName ? this.$vm.$refs[this.eventName].value : "",
            sCurNodeID: "",
            sExInfos: "",
          };

        //
        if (this.$route.query.sPID) {
          url = "wfr/send";
          param = {
            sPID: this.$route.query.sPID,
            sCurLinkID: this.$route.query.sCurLinkID || "",
            sExInfos: "",
          };
        }
        if (this.$vm && this.$vm.flows && this.$vm.flows.disposeIdea) {
          let disposeIdea = this.$vm.flows.disposeIdea;
          // 
          param.sOpinion = {
            Content: disposeIdea.inputValue,
            Attachments: disposeIdea.multipleFiles,
          };
          param.sOpinion = JSON.stringify(param.sOpinion);
        }
        if (line) {
          param.sLineIDs = line.LineID;
        }

        let config = {
          param: param,
          url: url,
        };

        // if(this.event.has(flowCtrlEvent.setSendConfig)){
        //   // 
        //   this.event.trigger(flowCtrlEvent.setSendConfig, { ui: this, config: config });
        // }

        this.$http
          .post(config.url, config.param)
          .then((response) => {
            this.handler(response);
          })
          .catch((response) => {
            dsf.layer.message("", false);
            this._RequestID = null;
          });
      } else {
        dsf.layer.message("''", false);
      }
    },
    handler(response) {
      if (response.data.state == 20000) {
        let data = response.data.data;
        this._RequestID = data.RequestID;
        if (
          data.Code == 20000 ||
          data.Code == 20001 ||
          data.Code == 20003 ||
          data.Code == 20004 ||
          data.Code == 20005
        ) {
          //  ||  ||  ||  || 
          this.directlySend(data);
          // this.root.close('3000');
          // this.event.trigger(flowCtrlEvent.changeState, {
          //   data: data,
          //   type: "",
          // });
        } else if (data.Code == 81000) {
          // 
          this.chooseSendNode(data);
        } else if (data.Code == 82000) {
          // 
          this.chooseSendTree(data);
        } else {
          let msg = null,
            code = data.Code;
          switch (code) {
            case 20006:
              msg = "";
              break;
            case 20007:
              msg = "";
              break;
            case 91000:
              msg = "";
              break;
            case 92000:
              msg = "";
              break;
            case 92001:
              msg = "";
              break;
            case 92002:
              msg = "";
              break;
            case 92003:
              msg = "";
              break;
            case 92004:
              msg = "";
              break;
            case 93000:
              msg = "";
              break;
            case 93001:
              msg = "";
              break;
            case 93002:
              msg = "";
              break;
            case 93003:
              msg = "";
              break;
            case 94001:
              msg = "";
              break;
            case 95001:
              msg = "";
              break;
            case 95002:
              msg = "";
              break;
            case 95003:
              msg = "";
              break;
            case 99000:
              msg = "";
              break;
          }
          dsf.layer.message(msg || "", false);
        }
      } else {
        this._RequestID = null;
        dsf.layer.message("", false);
      }
    },
    directlySend(data) {
      let list = [],
        _this = this;
      if (data.Code == 20000) {
        data.Links.forEach((line, index) => {
          if (line.Status == 0 && line.LinkUser) {
            let linkUser = line.LinkUser;
            let type = line.LinkUser.Type;
            if (type == 1) {
              // 
              list.push(linkUser.UserName);
            } else if (type == 2) {
              // +
              list.push(`${linkUser.DeptName}-${linkUser.RoleName}`);
            } else if (type == 3) {
              // 
              list.push(linkUser.JobName);
            } else if (type == 4) {
              // +
              list.push(`${linkUser.UserName}-${linkUser.RelationName}`);
            }
          }
        });
        dsf.layer.pc.openDialog({
          title: "",
          width: "300px",
          height: "100px",
          content: "SendFlowDialog",
          params: {
            list: list,
          },
          btns: [
            {
              text: "",
              class: "sendFlow",
              handler: (component) => {
                if (window.opener) {
                  window.close();
                }
                return true;
              },
            },
          ],
        });
      } else {
        let msg = "",
          code = data.Code;
        switch (code) {
          case 20004:
            msg = "";
            break;
          case 20005:
            msg = "";
            break;
        }
        dsf.layer.pc.openDialog({
          title: "",
          width: "300px",
          height: "100px",
          content: "SendFlowDialog",
          params: {
            msg: msg,
          },
          btns: [
            {
              text: "",
              class: "sendFlow",
              handler: (component) => {
                if (window.opener) {
                  window.close();
                }
                return true;
              },
            },
          ],
        });
      }
    },
    getSendBackLink(args) {
      let param = {
        sPID: args[0] || null,
        sCurLinkID: args[1] || null,
      };
      this.$http
        .post(dsf.url.getWebPath("wfr/getSendBackLink"), param)
        .then((res) => {
          if (res.data.success && res.data.data && res.data.data.BackLinks) {
            let BackLinks = res.data.data.BackLinks;
            if (BackLinks.length == 1) {
              this.sendBack(args, BackLinks[0].ID);
            } else {
              this.chooseBackNode(BackLinks, args);
            }
          } else {
            dsf.layer.message(res.data.Message || "", false);
          }
        })
        .catch((res) => {
          dsf.layer.message("", false);
        })
        .finally(() => {
          // dsf.layer.close(loadingIndex);
        });
    },
    sendBack(args, id) {
      let _this = this;
      dsf.layer
        .prompt("", "", {
          inputPlaceholder: "",
          inputType: "textarea",
          inputPattern: /\S/,
          inputErrorMessage: "",
          confirmButtonText: "",
        })
        .then(({ value }) => {
          let param = {
            sPID: args[0] || null,
            sCurLinkID: args[1] || null,
            sReason: value || null,
            sBackLinkID: id,
          };
          this.$http
            .post(dsf.url.getWebPath("wfr/sendBack"), param)
            .then((res) => {
              if (res.data.success && res.data.data && res.data.data.Code == "21000") {
                dsf.layer.message("", true);
                if (window.opener) {
                  window.close();
                }
              } else {
                dsf.layer.message(res.data.Message || "", false);
              }
            })
            .catch((res) => {
              dsf.layer.message("", false);
            });
        });
    },
    // 
    chooseBackNode(data, args) {
      dsf.layer.pc.openDialog({
        title: "",
        width: "300px",
        height: "180px",
        overflow: "hidden",
        content: "ChooseSendNode",
        params: {
          lines: data,
          callback: this.sendBack,
          requestID: this._RequestID,
          type: "back",
        },
        btns: [
          {
            text: "",
            class: "hide choose-send-node",
            handler: (component) => {
              if (component.validate()) {
                const line = component.yes();
                this.sendBack(args);
                return true;
              }
              return false;
            },
          },
          {
            class: "close-send-node",
            text: "",
          },
        ],
      });
    },
    chooseSendNode(data) {
      dsf.layer.pc.openDialog({
        title: "",
        width: "300px",
        height: "180px",
        overflow: "hidden",
        content: "ChooseSendNode",
        params: {
          lines: data.SendParams.Lines,
          callback: this.handler,
          requestID: this._RequestID,
          type: "next",
        },
        btns: [
          {
            text: "",
            class: "hide choose-send-node",
            handler: (component) => {
              if (component.validate()) {
                const line = component.yes();
                let param = {
                  sRequestID: this._RequestID,
                  sLineID: line.LineID,
                };
                this.$http.post("wfr/sendToLine", param).then((res) => {
                  if (res.data.state == 20000) {
                    this.handler(res);
                  }
                });
                return true;
              }
              return false;
            },
          },
          {
            class: "close-send-node",
            text: "",
          },
        ],
      });
    },
    chooseSendTree(data) {
      dsf.layer.pc.openDialog({
        title: "",
        width: "900px",
        height: "420px",
        overflow: "hidden",
        content: "ChooseSendTree",
        params: {
          lines: data.SendParams.Lines,
        },
        btns: [
          {
            text: "",
            handler: (component) => {
              if (component.validate()) {
                const result = component.yes();
                let param = {
                  sRequestID: this._RequestID,
                  sReceivers: result,
                };
                this.$http.post("wfr/sendToReceivers", param).then((res) => {
                  if (res.data.state == 20000) {
                    this.handler(res);
                  }
                });
                const closebtn = document.querySelector(".close-send-node");
                closebtn && closebtn.click();
                return true;
              }
              return false;
            },
          },
          {
            text: "",
          },
        ],
      });
    },
  },
});
</script>
